<template>
	<div :class="!haveBg ? 'header haveBgcolor' : 'header' " :style="{top:(showHeadr ? '0' : '-100px')}">
		<div class="box">
			<div class="Header_L">
				<img class="logo" @click="home" alt="返回首页" src="../assets/logo.png">
			</div>
			<ul class="Header_R">
				<li :class="index === showKey ? 'select' : ''" v-for="(item,index) in headerList" :key="index"
					@mouseenter="showList(index)" @mouseleave="hidenList()" @click="tolink(item)">
					{{ item.title }}
					<div v-if="item.childArr && item.childArr.length > 0 && index == showKey"
						class="second_nav animate__animated animate__fadeIn">
						<p v-for="(items,indexs) in item.childArr" :key="indexs" @click="tolink(items)">
							{{ items.title }}
						</p>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'HeaderView',
		data() {
			return {
				headerList: [{
						title: '首页',
						key: '0',
						url: '/'
					},
					{
						title: '关于公司',
						key: '1',
						childArr: [{
								title: '企业文化',
								url: '/firmCulture'
							},
							{
								title: '企业介绍',
								url: '/firmIntroduce'
							}
						]
					},
					{
						title: '产品',
						key: '2',
						url: "/project",
					},
					{
						title: '新闻与活动',
						key: '3',
						url: "/news",
					},
					{
						title: '招聘信息',
						url: "/job",
						key: '4'
					}
				],
				showKey: '',
				haveBg:false,
				showHeadr:true,
				noHome:false // 判断是否是首页 false不是 true 是
			}
		},
		watch:{
			$route(to,form){
				if(to.name !== 'home'){
					this.noHome = true
				}
			}
		},
		mounted() {
			// 监听滚轮
			window.addEventListener('mousewheel', this.handleScroll)
			
		},

		destroyed() {
			// 离开这个界面之后，删除滚动事件，不然容易除bug
			window.removeEventListener('scroll', this.handleScroll)
		},
		methods: {
			// 监听滚轮
			handleScroll(e) {
				if(this.noHome = true){
					this.haveBg = true
					this.showHeadr = true
					return
				}
				const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
				if(e.deltaY > 0){ // 向下
					
					if(scrollTop > 300){
						this.haveBg = true
					}else{
						this.haveBg = false
					}
					if(scrollTop > 800){
						this.showHeadr = false
					}
				}else{ // 向上
					this.showHeadr = true
					if(scrollTop > 100){
						this.haveBg = true
					}else{
						this.haveBg = false
					}
				}
			},
			handleScroll2(){
				var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
				if(scrollTop > 1500){
					this.showHeadr = false
				}else{
					this.showHeadr = true
				}
				if(scrollTop > 200){
					this.haveBg = true
				}
			},
			showList(index) {
				this.showKey = index
			},
			hidenList() {
				this.showKey = ''
			},
			tolink(item) {
				if (item.childArr && item.childArr.length > 0) {
					return
				}
				this.$router.push({
					path: item.url
				});
			},
			home() {
				this.$router.push({
					path: '/'
				});
			}
		}
	}
</script>
<style lange="less" scoped>
	.header {
		width: 100%;
		background-color: #ffdb00;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 99;
		transition: all 0.5s ease 0s;
	}
	.haveBgcolor{
		background-color: rgba(0,0,0,0)
	}
	.haveBgcolor .Header_R li{
		color: #fff;
	}
	.haveBgcolor .Header_R li .second_nav p {
		color: #0b3190;
	}
	.box {
		max-width: 1400px;
		margin: 0 auto;
		height: 90px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.Header_L {
		width: 10px;

	}

	.Header_L .logo {
		width: 251px;
		cursor: pointer;
	}

	.Header_R {
		display: flex;
		align-items: center;
		color: #0b3190;
		height: 100%;
		line-height: 90px;
	}

	.Header_R li {
		padding: 0 24px;
		cursor: pointer;
		position: relative;
		height: 100%;
		font-weight: bold;
	}

	.Header_R li.select {
		background-color: red;
	}

	.Header_R li .second_nav {
		position: absolute;
		padding: 10px;
		background: #fff;
		max-height: 450px;
		border-radius: 4px;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		top: 70px;
		font-weight: 400;
		box-shadow: 3px 5px 5px #d2d2d2;
	}

	.Header_R li .second_nav p {
		white-space: nowrap;
		font-size: 16px;
		line-height: 24px;
		text-overflow: ellipsis;
		overflow: hidden;
		cursor: pointer;
		border-radius: 40px;
		padding: 10px;
		text-align: center;
	}

	.Header_R li .second_nav p:hover {
		background: #ffdb00;
	}
</style>