import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import FirmCulture from '../views/firmCulture.vue'
import FirmIntroduce from '../views/firmIntroduce.vue'
import News from '../views/news.vue'
import NewInfo from '../views/newInfo.vue'
import Job from '../views/job.vue'
import Project from '../views/project.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/firmCulture', // 公司文化
    name: 'firmCulture',
    component: FirmCulture
  },
  {
    path: '/firmIntroduce', // 公司介绍
    name: 'firmIntroduce', 
    component: FirmIntroduce
  },
  {
    path: '/news', // 公司介绍
    name: 'news', 
    component: News
  },
  {
    path: '/newinfo', // 公司介绍
    name: 'newinfo', 
    component: NewInfo
  },
  {
    path: '/job', // 公司介绍
    name: 'job', 
    component: Job
  },
  {
    path: '/project', // 公司介绍
    name: 'project', 
    component: Project
  }
]

const router = new VueRouter({
  routes
})

export default router
